@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100..900&display=swap');
*{
    padding: 0;
    margin: 0;
}

body{
    width: 100%;
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
    color: #222;
    
}

header{
    font-weight: 500;
    font-size: 18px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 25px;
    background: #eee;
    /* margin-bottom: 15px; */
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
}

#logo {
    font-size: 24px;
}

#headerBar{
    margin-left: auto;
    float: right;
    display: flex;
    align-items: center;
}

@media screen and (min-width:400px){
    #navButton{
        display: none;
    }

    .navDrop{
        display: none;
    }

    ul {
        list-style: none;
        display: flex;
    }

    ul span{
        margin-left: 25px;
        cursor: pointer;
        position: relative;
        display: inline-block;
        text-align: center;
        
    }

    ul span::after{
        content: " ";
        background: rgb(89, 89, 89);
        height: 2px;
        width: 60%;
        position: absolute;
        right: 50%;
        transform: translateX(50%); 
        bottom: -5px;
        transition: width 0.3s;
    }


    ul span:hover::after{
        width: 90%;
    }
}
@media screen and (max-width:450px){
    #nav ul{
        display: none;
    }

    #navButton{
        display: block;
        width: 30px;
        height: 30px;
        background-image: url('/src/img/navigation.png');
        background-repeat: no-repeat;
        background-position: center;
        border: 1px solid #222;
        border-radius: 50%;
        padding: 3px; 
        background-size: 20px;
        transition: box-shadow 0.3s;
    }

    #drop {
        position: relative;
    }

    .navDrop{
        display: block;
        position: absolute;
        background: #eee;
        color: #222;
        width: 100px;
        height: 26px;
        font-size: 16px;   
        display: flex;
        align-items: center;
        padding: 5px 0 5px 4px;
        border: 1px solid #222;
        cursor: pointer;
    }

    #f {
        top: 8px;
        left: -10px;
    }

    #s {
        top: 44px;
        left: -10px;
    }

    #t {
        top: 80px;
        left: -10px;
    }

}

a{
    text-decoration: none;
}

a{
    color: #222
}

#notFoundPage p{
    font-size: 18px;
    text-align: center;
    margin: 10px 0 0;
}

#notFoundPage p a {
    font-weight: 600;
    font-size: 22px;
}

#bascet {
    padding: 3px;
    width: 30px;
    height: 30px;
    margin-left: 20px;
    border: 1px solid #222;
    border-radius: 50%;
    background-image: url('/src/img/bascet.png');
    background-repeat: no-repeat;
    background-position: center;
    transition: box-shadow 0.3s;
}

#bascet:hover, #navButton:hover{
    box-shadow: 0 0 8px #222;
}


@media screen and (max-width:520px) and (min-width: 450px){
    header{
        font-size: 16px;
    }

    ul span{
        margin-left: 12px;
    }

}

#page{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

main, #contactsPage, #infoPage, #bascetPage, #notFoundPage, #itemDescriptionPage{
    margin-top: 50px;
    /* padding-bottom: 115px; */
    padding: 20px 25px;
}

#itemDescriptionPage{
    padding: 20px 35px;
}

main h2, #contactsPage h2, #infoPage h2, #bascetPage h2, #notFoundPage h2 {
    font-size: 28px;
    text-align: center;
    letter-spacing: 1px;
}

#bascetPage #linkToCatalog {
    text-align: center;
    font-size: 18px;
}

#bascetPage p a{
    font-size: 20px;
    font-weight: 600;

}

#searchBar {
    margin: 15px 0 25px;
    width: 100%;
    padding: 5px 15px;
    box-sizing: border-box;
}

#searchBar input {
    width: 100%;
    padding: 6px 10px;
    box-sizing: border-box;
    font-size: 16px;
}

#searchBar input:invalid {
    box-shadow: none;
}

.products{
    display: flex;
    justify-content: flex-start;
    align-items: stretch;
    flex-wrap: wrap;
}

.item{
    margin-right: 10px;
    text-align: left;
    background-color: #eee;
    box-sizing: border-box;
    border-radius: 0 0 15px 15px;
    margin-bottom: 15px;
    border: 1px solid #222;
    transition: transform 0.3s, box-shadow 0.5s;
    /* align-self: stretch; */
    position: relative;
}

.item:hover{
    transform: scale(1.05);
    cursor: pointer;
    box-shadow: 0 0 25px #777;
}

.item img {
    width: 100%;
    /* height: 75%; */
}

.productDesc{
    padding: 10px 15px 40px;
    overflow: hidden;
}

@media screen and (max-width:749px) {
    
    .item{
        width: calc(50% - 5px);
    }
    .item:nth-child(even){
        margin-right: 0;
        /* background: red; */
    }
}

@media screen and (min-width:750px) and (max-width:1199px){

    main h2{
        font-size: 28px;
    }

    .main{
        padding: 20px 30px;
    }

    .item{
        width: calc(33% - 6px);
    }

    .item:nth-child(3n){
        margin-right: 0;
        /* background: red; */
    }
}

@media screen and (min-width:1200px) {

    main h2{
        font-size: 34px;
    }

    .main{
        padding: 25px 90px;
    }
    .item{
        width: calc(25% - 19px);
        /* min-height: 450px; */
        margin-right: 25px;
        margin-bottom: 25px;
    }

    .item:nth-child(4n){
        margin-right: 0;
        /* background: red; */
    }
}

.title{
    font-size: 24px;
    font-weight: 600;
}

.desc{
    font-size: 16px;
    margin: 5px 0;
}

.price{
    font-size: 20px;
    color: rgb(0, 184, 0);
    position: absolute;
    right: 15px;
    bottom: 10px;
}

.toBascet{
    position: absolute;
    width: 30px;
    height: 30px;
    background: red;
    bottom: 8px;
    left: 10px;
    border-radius: 50%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 20px;
    box-sizing: border-box;
    transition: box-shadow 0.3s;
}

.toBascet::after{
    content: '+';
}

.toBascet:hover{
    box-shadow: 0 0 15px red;
    
}

.toBascet:active{
    transform: scale(0.9);
}

#productsInBascet {
    display: flex;
    flex-direction: column;
}

.itemInBascet{
    border: 1px solid grey;
    width: 100%;
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px 0 35px;
    box-sizing: border-box;
}

.itemInBascet img {
    max-height: 100%;
    border-radius: 15px;
    /* max-width: 100%; */
}

.itemInBascet a {
    width: 100%;
    height: 75%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.itemInBascet .img{
    display: flex;
    justify-content: center;
    align-items: center;
    /* max-width: 40%; */
    height: 100%;
}

.itemInBascet .itemDescInBascet {
    padding: 0 20px 0 25px;
    font-size: 18px;
    width: 50%;
    box-sizing: border-box;
}

.itemInBascet .titleInBascet {
    font-size: 28px;
}

.itemInBascet .descInBascet {
    margin: 15px 0;
}

.itemInBascet .priceInBascet {
    font-size: 20px;
    color: rgb(0, 184, 0);
}

.itemInBascet .priceInBascet span, .itemInBascet .titleInBascet span{
    font-size: 16px;
    color: #222;
    padding-left: 5px;
}


.itemInBascet .deleteItemFromBascet {
    width: 10%;
    max-height: 50px;
    min-height: 30px;
    background: red;
    cursor: pointer;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.itemInBascet .deleteItemFromBascet::after{
    content: '';
    width: 40%;
    height: 4px;
    border-radius: 5px;
    background: #eee;
    font-size: 24px;
}


@media screen and (max-width: 600px){

    .itemInBascet{
        border: 1px solid grey;
        width: 100%;
        min-height: 200px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 15px 0 20px;
        box-sizing: border-box;
    }
    
    .itemInBascet img {
        max-height: 150px;
        border-radius: 15px;
    }
    
    .itemInBascet .itemDescInBascet {
        padding: 0 15px 0 20px;
        width: 50%;
        box-sizing: border-box;
    }

    .itemInBascet .titleInBascet {
        font-size: 22px;
    }
    
    .itemInBascet .descInBascet {
        margin: 15px 0;
        font-size: 17px;
    }
    
    .itemInBascet .priceInBascet {
        font-size: 20px;
        color: rgb(0, 184, 0);
    }
    
    .itemInBascet .priceInBascet span, .itemInBascet .titleInBascet span{
        font-size: 16px;
        color: #222;
        padding-left: 5px;
    }

}


@media screen and (max-width: 450px){

    .descInBascet{
        display: none;
    }

    .priceInBascet{
        margin-top: 15px;
    }

    .deleteItemFromBascet{
        width: 10%;
        height: 10%;
    }


}

#bascetPage #result {
    margin: 20px 0;
    font-size: 24px;
}

#bascetPage #clearBascet{
    width: 115px;
    height: 35px;
    background: red;
    border-radius: 10px;
    color: #eee;
    font-size: 18px;
    padding: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    cursor: pointer;
    transition: box-shadow 0.3s;
}

#bascetPage #clearBascet:hover{
    box-shadow: 0 0 15px red;
}


footer{
    width: 100%;
    background: #ccc;
    box-sizing: border-box;
    padding: 30px 35px;
    color: #444;
    margin-top: auto;
}

footer b{
    display: inline-block;
    font-size: 16px;
    margin-bottom: 15px;
    font-weight: 700;
    
}

footer p{
    letter-spacing: 1px;
}

footer b i {
    font-style: normal;
    color: rgba(2,2,2,0.3);
    font-size: 12px;
}

#notFoundPage{
    display: flex;
    flex-direction: column;
    align-items: center;
}

#notFoundPage img {
    width: 40%;
    height: 40%;
}


#infoPage, #contactsPage{
    display: flex;
    flex-direction: column;
    align-items: center;
}


#infoPage p, #contactsPage p{
    font-size: 24px;
    margin: 10px 0 10px;
}

#infoPage img, #contactsPage img{
    width: 45%;
    height: 40%;
}

#itemDescriptionPage{
    display: flex;
    flex-direction: column;
    align-items: center;
}

#itemDescriptionPage img {
    max-width: 55%;
    max-height: 350px;
    min-height: 250px;
    border-radius: 25px;
    margin-bottom: 15px;
    margin-right: 15px;
    border: 1px solid #222;
}

#itemDescriptionPage #itemFullDesc {
    width: 100%;
}

#itemDescriptionPage #headOfFullDesc{
    display: flex;
    /* justify-content: center; */
    align-items: center;
    font-size: 36px;
    margin-bottom: 15px;
    font-weight: 600;
}


#headDesc {
    display: flex;
    align-content: space-between;
    flex-wrap: wrap;
}


#itemDescriptionPage #headOfFullDesc p:last-of-type{
    margin-bottom: 25px;
}

#itemDescriptionPage #itemToBascet {
    font-weight: 600;
    color: #eee;
    font-size: 18px;
    background: red;
    border-radius: 15px;
    width: 60%;
    padding: 8px 5px;
    text-align: center;
    transition: box-shadow 0.3s;
    cursor: pointer;
}

#itemDescriptionPage #itemToBascet:hover {
    box-shadow: 0 0 15px red;
}

#itemDescriptionPage #headOfFullDesc p:last-of-type{
    color: rgb(0, 184, 0);
}

#itemDescriptionPage #itemDesc {
    width: 100%;
    font-size: 23px;
}

#itemDescriptionPage #attributes {
    padding-top: 20px;
}

#itemDescriptionPage .attribute{
    margin-bottom: 10px;
}

#itemDescriptionPage .attribute p:first-of-type{
    font-size: 20px;
    margin-bottom: 5px;
}

#itemDescriptionPage .attribute p:last-of-type{
    padding-left: 15px;
    font-size: 18px;
}

@media screen and (max-width: 450px){
    
    #itemDescriptionPage #headOfFullDesc{
        font-size: 26px;
    }
    
    #itemDescriptionPage #headOfFullDesc p:last-of-type{
        margin-bottom: 15px;
    }
    
    #itemDescriptionPage #itemToBascet {
        font-size: 14px;
    }
    
    #itemDescriptionPage #itemDesc {
        font-size: 20px;
    }
    
    #itemDescriptionPage .attribute p:first-of-type{
        font-size: 18px;
    }
    
    #itemDescriptionPage .attribute p:last-of-type{
        padding-left: 15px;
        font-size: 16px;
    }

}